html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

body {
  font-family: "Lexend", sans-serif;
  font-size: 1.6rem;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #e8505b;
}

.bg-blanco {
  background-color: #FFFFFF;
}

.margen-menos-10 {
  margin-top: -10rem;
}

.pad-bot-0 {
  padding-bottom: 0 !important;
}

.mar-ar-ab-2 {
  margin: 2rem 0;
}

.seccion {
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .seccion {
    padding: 5rem 0;
  }
}

.h2-sombra1 h2 {
  font-size: 3.6rem;
  display: inline-block;
}
.h2-sombra1 h2::after {
  content: "";
  background-color: #e8505b;
  height: 1rem;
  width: 105%;
  display: block;
  margin: -1.4rem 0 0 0;
}

.h2-sombra2 h2 {
  font-size: 3.6rem;
  display: inline-block;
}
.h2-sombra2 h2::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: 105%;
  display: block;
  margin: -1.4rem 0 0 0;
}

.degradado {
  color: transparent;
  background: linear-gradient(to right, #e8505b 0%, #bd1e59 100%);
  -webkit-background-clip: text;
  background-clip: text;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

p {
  color: #000000;
  font-size: 2rem;
}

a {
  text-decoration: none;
}

/* Añade a este archivo los demas archivos de esta carpeta con @forward y recuerda añadadirlos al archivo principal (app.scss) */
.header {
  background-image: url(/build/img/header.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 3rem 0 20rem 0;
}
.header .header__busqueda {
  margin-top: 4rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header .header__busqueda {
    margin-top: 20rem;
  }
}
.header .header__busquedaBoton {
  border-radius: 5rem;
  background-color: #FFFFFF;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #e8505b;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.header .header__busquedaBoton:hover {
  cursor: pointer;
  background-color: #e6e6e6;
}
@media (min-width: 768px) {
  .header .header__busquedaBoton {
    width: auto;
    display: inline-block;
  }
}
.header .header__busquedaBoton:hover {
  background-color: #e8505b;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.header .header__busquedaBoton:hover:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .header .header__busquedaBoton:hover {
    width: auto;
    display: inline-block;
  }
}

.barra {
  display: none;
}
@media (min-width: 1024px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .barra__nombre {
  color: #e8505b;
  font-size: 3rem;
  margin: 0;
}
.barra .barra__navegacion {
  display: flex;
  gap: 4rem;
}
.barra .barra__enlace {
  color: #FFFFFF;
}

.formulario {
  margin: 5rem auto 2rem auto;
  background-color: #FFFFFF;
  border-radius: 5rem;
  padding: 3rem 0;
  box-shadow: 0px 3px 7px 0px #686868;
}
@media (min-width: 1024px) {
  .formulario {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 90rem;
    padding: 0;
  }
}
.formulario .formulario__campo {
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .formulario .formulario__campo:nth-child(1) {
    padding-left: 4rem;
  }
}
.formulario .formulario__campo:hover {
  background-color: #f0f0f0;
  border-radius: 5rem;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.4);
}
.formulario .formulario__label {
  font-size: 1.2rem;
  font-weight: 900;
}
.formulario .formulario__input {
  border: none;
  width: 100%;
  color: #686868;
}
.formulario .formulario__submit {
  display: block;
  height: 4rem;
  border: none;
  border-radius: 2rem;
  background-image: radial-gradient(circle, #de3071 0%, #911744 100%);
  background-size: cover;
  padding: 0 2rem;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.2rem;
}

.destinos .destinos__heading {
  margin: 2rem 0;
}
.destinos .destinos__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .destinos .destinos__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .destinos .destinos__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.destinos .destino {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
.destinos .destino .destino__imagen {
  width: 14rem;
  height: 14rem;
}
.destinos .destino .destino__nombre {
  margin: 0;
}
.destinos .destino .destino__distancia {
  margin: 0;
}

.lugares .lugares__grid {
  display: grid;
  grid-template-columns: repeat(4, 30rem);
  column-gap: 4rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.lugares .lugares__grid > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .lugares .lugares__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    overflow-x: hidden;
  }
}
@media (min-width: 1024px) {
  .lugares .lugares__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    overflow-x: hidden;
  }
}
.lugares .lugar .lugar__imagen {
  border-radius: 1rem;
}
.lugares .lugar .lugar__nombre {
  font-size: 1.8rem;
}

.anfitrion {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  row-gap: 5rem;
  padding: 5rem;
  border-radius: 1rem;
  background-image: linear-gradient(to bottom, #000000 0%, #000000 50%, transparent 50%), url(/build/img/anfitrion.jpg);
  background-repeat: no-repeat;
  background-position: center, center bottom;
  background-size: 100%, 60rem;
}
@media (min-width: 480px) {
  .anfitrion {
    background-size: 100%, 70rem;
    background-position: center, center 200%;
  }
}
@media (min-width: 768px) {
  .anfitrion {
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
    background-image: linear-gradient(to right, #000000 0%, #000000 45%, transparent 55%), url(/build/img/anfitrion.jpg);
    background-repeat: no-repeat;
    background-position: center, -10% 10%;
    background-size: 100%, auto;
    padding: 15rem 5rem;
  }
}
@media (min-width: 1024px) {
  .anfitrion {
    background-position: center, 150% 20%;
  }
}
.anfitrion .anfitrion__heading {
  color: #FFFFFF;
}
.anfitrion .anfitrion__texto {
  color: #FFFFFF;
}
.anfitrion .anfitrion__enlace {
  background-color: #FFFFFF;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #000000;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border-radius: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}
.anfitrion .anfitrion__enlace:hover {
  cursor: pointer;
  background-color: #e6e6e6;
}
@media (min-width: 768px) {
  .anfitrion .anfitrion__enlace {
    width: auto;
    display: inline-block;
  }
}

.experiencias .experiencias__grid {
  display: grid;
  grid-template-columns: repeat(3, 30rem);
  column-gap: 2rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.experiencias .experiencias__grid > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .experiencias .experiencias__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    overflow-x: hidden;
  }
}
.experiencias .experiencia .experiencia__imagen {
  border-radius: 1rem;
}
.experiencias .experiencia .experiencia__nombre {
  font-size: 2rem;
  font-weight: 700;
}
.experiencias .experiencia .experiencia__texto {
  font-size: 1.8rem;
}

.footer {
  background-color: #e1e1e1;
  border-top: 1px solid #686868;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .footer .footer__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .footer .footer__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.footer .footer__seccion .footer__heading {
  margin: 2rem 0;
  text-transform: uppercase;
  font-size: 1.2rem;
}
.footer .footer__seccion .footer__enlace {
  display: block;
  color: #686868;
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.footer .footer__barra {
  border-top: 1px solid #686868;
  margin-top: 1rem;
  padding: 2rem 0;
}
.footer .footer__barra .footer__baranav {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .footer .footer__barra .footer__baranav {
    flex-direction: row;
  }
}
.footer .footer__barra .footer__baranav .footer__enlace--barra {
  color: #686868;
}
.footer .footer__barra .footer__baranav .footer__enlace--barra::before {
  content: "-";
  margin-right: 1rem;
}

